import Typography from 'components/ui/Typography';

export type TSolutionItem = {
  recommend: string;
  seedMoney: string;
  additional: string;
};

interface SolutionTableProps {
  offTrack: TSolutionItem;
  onTrack: TSolutionItem;
}

const formatter = (value: string) => {
  if (value === '') {
    return '-';
  } else {
    return value;
  }
};

const currencyFormatter = (value: string) => {
  if (value === '') {
    return '-';
  } else {
    return `${value} $`;
  }
};

